
.flex-column {
  display: flex;
  padding-right: 1rem;
  margin-bottom: 1rem;
  flex-direction: column;
}

.membership-form {
  max-width: 720px;
  width: 100%;
  padding: 2rem 0.5rem;
  margin-left: auto;
  margin-right: auto;
}

h3 {
  font-size: 32px;
}

.register > .flex-container {
  flex-wrap: nowrap;
}

.register .register-info {
  background-color: black;
  min-height: 100vh;
  max-width: 500px;
  width: 100%;
}

.register p.description {
  max-width: 420px;
  margin-bottom: 1rem;
}

.register input{
  margin: 0;
}

.register label {
  font-size: 13px;
  color: rgb(107, 107, 107);
  font-weight: 600;
}

.register input.text-input {
  margin-right: 0.5rem;
  height: 32px;
  max-width: 420px;
  padding: 0 0.5rem;
  border: none;
  border-bottom: 2px solid rgb(26, 117, 255);
}

.register .flex-container {
  justify-content: space-between;
}

.register input::placeholder {
  font-family: 'Poppins', sans-serif;
  font-size: 13px;
}

.register .register-btn {
  display: block;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
  margin-top: 1rem;
  width: 100%;
  padding: 0.5rem 1rem;
  background-color: blue;
  color: #fff;
  border: none;
  border-radius: 2px;
}