@media print {
  .navbar , .print-btn, .nav-button{
    display: none !important;
  }
  
}

table {
    border-collapse: collapse;
    width: 100%;
  }

  td, th {
    border: 1px solid #aaa;
    text-align: left;
    font-size: 13px;
    padding: 2px 8px;
  }
  
  .form-output {
    position: relative;
    max-width: 700px;
    margin-left: auto;
    height: 1000px;
    margin-right: auto;
  }

  .form-output .background-logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 80%;
    opacity: 0.2;
    z-index: -1;
  }

  .form-output .img-logo {
    width: 90px;
    margin-right: 20px;
  }

  .form-heading {
    display: flex;
    align-items: center;
    text-align: center;
    width: 100%;
  }
  .checkbox .bi {
    margin-right: 0.35rem;
  }

  .form-output .flex-4 {
    display: flex;
    align-items: center;
  }

  .form-output span {
    font-size: 14px;
  }

  .form-output .photo-frame {
    font-size: 13px;
    max-width: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    aspect-ratio: 2 / 2;
    width: 100%;
    border: 1px solid #ccc;
  }

  .signature-field {
    margin-top: 55px;
  }

  .signature-field span {
    padding: 0.5rem 2rem;
    padding-bottom: 0;
    text-align: center;
    display: block;
    border-top: 1px solid #ccc;
  }

  .form-output h5 {
    font-size: 14px;
  }

  .plan-payment {
    margin-bottom: 1.5rem;
  }
  .plan-payment td{
    height: 24px;
  }

  .form-output .input-box {
    height: 18px;
    width: 150px;
    display: inline-block;
    margin-left: 10px;
    border-bottom: 1px solid rgb(73, 73, 73);
  }

  .form-output .table-50 th, .form-output .table-50 td {
    width: 50%;
  }

  .form-output .nav-button {
    float: right;
  }