@import url('http://fonts.googleapis.com/css?family=Poppins:400,500,600');

*, *::after, *::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  width: 100%;
  margin-bottom: 0.5rem;
}

p {
  width: 100%;
  margin: 0;
  margin-bottom: 1rem;
  font-size: 15px;
  color: #363636;
}

.container {
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}

.navbar {
  padding: 0.5rem 1rem;
}

.navbar .container {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.flex-4 {
  max-width: 33.33%;
  flex: 0 0 33.33%;
}

.flex-6 {
  max-width: 50%;
  flex: 0 0 50%;
}

.flex-8 {
  max-width: 66.66%;
  flex: 0 0 66.66%;
}

.mb-0 {
  margin-bottom: 0;
}

.mb-1 {
  margin-bottom: 0.25rem;
}

.mb-2 {
  margin-bottom: 0.25rem;
}


.mt-3 {
  margin-top: 0.75rem;
}

.font-bold {
  font-weight: 600;
}